import React from "react";
import './Skills.css';
import {Card} from "react-bootstrap";
import {ProgressBar} from "react-bootstrap";
import SkillImg from '../../images/skill1.png';

function Skills(){
    return (
        <div className="wrapper container-fluid skill-section text-center" id="Skills">
            <h3>My Skills</h3>
            <div className="row skills text-center">
              <div className="col-lg-6 col-md-6 col-sm-6 image">
                <img src={SkillImg} />
              </div>
              <div className="col-lg-6 col-md-6 col-sm-6 text-center">
                <ProgressBar animated variant="primary" now={85} label={"HTML5"}></ProgressBar>
                <ProgressBar animated variant="secondary" now={85} label={"CSS3"}></ProgressBar>
                <ProgressBar animated variant="success" now={77} label={"JAVASCRIPT"}></ProgressBar>
                <ProgressBar animated variant="danger" now={75} label={"BOOTSTRAP"}></ProgressBar>
                <ProgressBar animated variant="warning" now={62} label={"REACT JS"}></ProgressBar>
                <ProgressBar animated variant="info" now={76} label={"CODEIGNITER HMVC"}></ProgressBar>
                <ProgressBar animated variant="dark" now={75} label={"MYSQL"}></ProgressBar>
                <ProgressBar animated variant="custompurple" now={80} label={"PHOTOSHOP"}></ProgressBar>
              </div>
             </div>
        </div>
    );
}

export default Skills;