import React from "react";
import './Otherwork.css';
import Html from '../../images/html.png';
import Psbanner from '../../images/psbanner.jpeg';
import Psposter from '../../images/psposter.jpeg';
import Pstemp from '../../images/pstemp.jpeg';
import Psvisiting from '../../images/psvisiting.jpeg';
import Wdlanding from '../../images/wdlanding.jpeg';
import Wdlanding1 from '../../images/wdlanding1.jpeg';
import Wdlanding2 from '../../images/wblanding2.jpeg';
import Wdlogin from '../../images/wdlogin.png';
import {Card} from "react-bootstrap";
import {Tab} from "react-bootstrap";
import {Tabs} from "react-bootstrap";

function Otherwork(){
    return (
        <div className="wrapper contaioner-fluid text-center otherwork-section" id="MyWork">
            <h3>My Work</h3>
            <Tabs pullRight defaultActive="photoshop" className="mb-3">
                <Tab eventKey="photoshop" title="Photoshop">
            <div className="row">
            <div className="col-lg-3 col-md-3 col-sm-3 skill-card">
<Card className="card">
    <Card.Img src={Psbanner} />
  </Card>
</div>
<div className="col-lg-3 col-md-3 col-sm-3 skill-card">
  <Card className="card">
    <Card.Img src={Psposter} />
  </Card>
</div>
<div className="col-lg-3 col-md-3 col-sm-3 skill-card">
  <Card className="card">
    <Card.Img src={Pstemp} />
  </Card>
</div>
<div className="col-lg-3 col-md-3 col-sm-3 skill-card">
<Card className="card">
    <Card.Img src={Psvisiting} />
  </Card>
</div>
</div>
</Tab>
<Tab eventKey="webdesigning" title="Web Designing">
            <div className="row">
            <div className="col-lg-3 col-md-3 col-sm-3 skill-card">
<Card className="card">
    <Card.Img src={Wdlanding} />
  </Card>
</div>
<div className="col-lg-3 col-md-3 col-sm-3 skill-card">
  <Card className="card">
    <Card.Img src={Wdlanding1} />
  </Card>
</div>
<div className="col-lg-3 col-md-3 col-sm-3 skill-card">
  <Card className="card">
    <Card.Img src={Wdlanding2} />
  </Card>
</div>
<div className="col-lg-3 col-md-3 col-sm-3 skill-card">
  <Card className="card">
    <Card.Img src={Wdlogin} />
  </Card>
</div>
</div>
</Tab>
</Tabs>
</div>

    );
}
export default Otherwork;