import React from "react";
import './Projects.css';
import Image from '../../images/bg.jpg';
import Calculator from '../../videos/calculator.mp4';
import Netflix from '../../videos/netflix.mp4';
import WordCount from '../../videos/wordcounter.mp4';
import Chatbot from '../../videos/chatbot.mp4';
import {Carousel} from "react-bootstrap"
function Projects(){
    return (
        <div className="wrapper container-fluid text-center work-section">
            <h3>Other Work</h3>
            <div className="text-center"> 
               <Carousel interval={null}>
      <Carousel.Item>
      <video className="d-block w-100" src={Netflix} loop autoPlay muted></video>
      </Carousel.Item>
      <Carousel.Item>
      <video className="d-block w-100" src={WordCount} loop autoPlay muted></video>
      </Carousel.Item>
      <Carousel.Item>
        <video className="d-block w-100" src={Calculator} loop autoPlay muted></video>
      </Carousel.Item>
      <Carousel.Item>
        <video className="d-block w-100" src={Chatbot} loop autoPlay muted></video>
      </Carousel.Item>
    </Carousel></div>
        </div>
    );
}
export default Projects;